import React from "react";
import { Carousel } from "react-responsive-carousel";

class ARG extends React.Component{

  constructor(){
		super();
		this.state={

		}
	}

  render(){
    function changeCurrentId(e){
      gino.updateCurrentId(imgArr[e].key)
    }
    let gino = this.props;

    const arrayRobotsAsc = gino.robots;
  	const arrayRobotsDesc = [];



    let lunghezzaCarousel = gino.inWidth;
    parseInt(lunghezzaCarousel, 10);
    lunghezzaCarousel = lunghezzaCarousel*0.9;
    lunghezzaCarousel = lunghezzaCarousel + "px";

    let altezzaCarousel = gino.inHeight;
    parseInt(altezzaCarousel, 10);
    altezzaCarousel = altezzaCarousel*0.9;

    let numSalvato = 0;


    const imgStyle={
      maxHeight: `${altezzaCarousel}px`,
      width: 'auto',
    }

    //inizio linkSet



      //fine linkSet

    let imgArr = [];

  	for (var i = arrayRobotsAsc.length - 1; i >= 0; i--) {

      // eslint-disable-next-line
      let linkMOD ="";
    	const lInk = arrayRobotsAsc[i].link;
    	var pos = lInk.lastIndexOf("/");
    	let oKGIF = true;
    	const lInkpart2 = lInk.slice(pos, lInk.length);
    	const lInkpart3 = lInkpart2.slice((lInkpart2.length-3), lInkpart2.length);
    	if(lInkpart3==='gif')
    		oKGIF= false;

    	let lInkMOD = lInk;
    		if(gino.type!=="mobile"){
    			//console.log("desk///////////////")

    		}else if(oKGIF){
    			//console.log("mobile///////////////")
    			const lInkpart1Mobile = lInk.slice(0, pos);
    			const lInkpart2Mobile = lInk.slice(pos, lInk.length);
    			lInkMOD = lInkpart1Mobile + "/okMobile" + lInkpart2Mobile;
    			if(lInkMOD==="/okMobile")
    				linkMOD = "";
    			else
    				linkMOD = lInkMOD;
    		}


      //
      imgArr.push(<img alt={arrayRobotsAsc[i].id} id={arrayRobotsAsc[i].id} key={arrayRobotsAsc[i].id} style={imgStyle} src={lInkMOD} />)
  		arrayRobotsDesc[arrayRobotsAsc.length-1-i]=arrayRobotsAsc[i];
  	}

    if(imgArr.length!==0){


      for (var i2 = imgArr.length - 1; i2 >= 0; i2--) {
        if(imgArr[i2].key===gino.currentImg)
    		  numSalvato = i2;
    	}
      





  return(
    <Carousel
    useKeyboardArrows={true}
    emulateTouch={true}
    showStatus={false}
    showIndicators={false}
    showThumbs={false}
    swipeable={true}
    className="carouselMio"
    dynamicHeight={true}
    selectedItem={numSalvato}
    //onClickItem={swipeToRight}
    onChange={(e)=>{changeCurrentId(e)}}
    width={lunghezzaCarousel}
    >

      {imgArr}



    </Carousel>
  );
  }
  else
  {return(<div id="niente di niente"></div>);}

  }

}


export default ARG;



